<!-- 컴포넌트 UI 정의 -->
<template>
  <div>
    <div>
      <div style="display: flex" class="justify-space-between">
        <div v-for="large of largeArray" :key="large">
          <v-btn
            text
            tile
            color="black"
            height="48px"
            :width="Math.floor(100 / largeArray.length).toString() + `vw`"
            style="font-weight: bolder"
            @click="theme = large"
          >
            {{ large }}
          </v-btn>
        </div>
      </div>
    </div>
    <div>
      <div v-for="category of categories" :key="category.clarge" v-show="theme === category.clarge">
        <div v-for="mid of category.cmedium" :key="mid.cmedium">
          <div class="text-center my-2">
            <v-chip style="font-weight: bolder">{{ mid.cmedium }}</v-chip>
          </div>
          <v-slide-group class="pa-2" active-class="success">
            <v-slide-item
              v-for="(item, i) in product[category.clarge][mid.cmedium]"
              :key="item.pid"
            >
              <div class="mx-2 text-center">
                <div style="width: 150px" @click="moveProductDetail(item.pid)">
                  <div class="mx-auto">
                    <v-img
                      :src="item.colors[0].cimage1"
                      lazy-src="@/assets/images/event/eventloader.jpg"
                    >
                      <div
                        class="py-2 text-center"
                        style="
                          width: 40px;
                          background-color: black;
                          color: white;
                          font-weight: bolder;
                        "
                      >
                        {{ i + 1 }}
                      </div>
                      <template v-slot:placeholder>
                        <v-row class="fill-height fill-width ma-0" align="center" justify="center">
                          <v-progress-circular
                            indeterminate
                            color="grey lighten-5"
                          ></v-progress-circular>
                        </v-row>
                      </template>
                    </v-img>
                  </div>
                  <div>
                    <div class="text-truncate" style="font-size: 0.875rem; font-weight: bolder">
                      {{ item.bname }}
                    </div>
                    <div class="text-truncate" style="font-size: 0.7rem">
                      {{ item.pname }}
                    </div>
                    <div class="text-truncate" style="font-size: 0.875rem; font-weight: bolder">
                      {{ item.pprice.toLocaleString() }}원
                    </div>
                  </div>
                </div>
              </div>
            </v-slide-item>
          </v-slide-group>
        </div>
      </div>
    </div>
  </div>
</template>

